.todo-container{position:relative;max-width:370px;width:370px;background:#fff;border-radius:0 0 5px 5px}.todo-container#todo{border-top:2px solid #920000}.todo-container#inProgress{border-top:2px solid #ffa600}.todo-container#complete{border-top:2px solid #488c5c}.todo-container#complete .todo-item{padding-left:20px;text-decoration:line-through;color:#888}.todo-container#complete .todo-item::after{content:"âœ”";left:7px;transform:translateY(calc(-50% + 2px))}.todo-container#complete .todo-item .text{color:#888}.todo-container h1{top:10px;font-size:25px;text-align:center;white-space:nowrap;font-weight:700;color:#fff;padding:20px 0;text-transform:uppercase;background:#123}.todo-container p{text-align:center;padding-top:20px;padding-bottom:10px;margin:0 0 10px 0}.todo-container .here{border:2px dashed #333}.todo-container .here::after{content:"";position:absolute;height:100%;width:100%;bottom:-2px;border-bottom:5px solid rgba(136,136,136,.3);filter:blur(2px)}.todo-container .todo-item{position:relative;min-height:40px;display:flex;justify-content:space-between;padding:10px 10px 10px 10px;cursor:move}.todo-container .todo-item::after{content:"";position:absolute;height:20px;width:20px;left:0px;top:50%;transform:translateY(-50%)}.todo-container .todo-item .text{align-self:flex-end;display:flex;align-items:flex-end;font-family:"Roboto";color:#2b2f3a;font-size:14.5px;min-height:23px;padding:4px 4px 2px 3px;line-height:normal;width:100%;overflow:hidden;margin-bottom:6px;margin-right:5px}.todo-container .todo-item textarea{align-self:flex-end;resize:none;outline:none;width:100%;height:23px;margin-right:5px;font-size:16px;overflow:hidden;border:none;color:#888;font-size:15px;font-family:"Roboto";line-height:normal;font-style:italic;margin-bottom:6px}.todo-container .todo-item .btns{visibility:hidden;z-index:500;background:#fff;position:absolute;right:-38px;border-radius:2px;transition:.3s ease-out;box-shadow:0px 2px 2px 0 rgba(43,47,58,.2)}.todo-container .todo-item .move,.todo-container .todo-item .edit,.todo-container .todo-item .remove,.todo-container .todo-item .more{display:flex;justify-content:flex-end;align-items:center;height:40px;width:40px;background:none;border:none;color:#2b2f3a;cursor:pointer}.todo-container .todo-item .move *,.todo-container .todo-item .edit *,.todo-container .todo-item .remove *,.todo-container .todo-item .more *{transition:.3s}.todo-container .todo-item .more{z-index:500;justify-content:center;width:40px;margin-right:-10px;font-size:20px}.todo-container .todo-item .more *{margin-bottom:-7px}.todo-container .todo-item .more:hover+.btns,.todo-container .todo-item .btns:hover{visibility:visible}.todo-container .todo-item .edit{font-size:29.5px;color:#fff}.todo-container .todo-item .edit *{padding:7px;background:#2b2f3a;border-radius:4px}.todo-container .todo-item .edit:hover *{background:#e49400}.todo-container .todo-item .edit .done:hover{background:#488c5c}.todo-container .todo-item .move{font-size:30px}.todo-container .todo-item .move:hover{color:#488c5c}.todo-container .todo-item .remove{font-size:30px;color:#fff}.todo-container .todo-item .remove *{background:#2b2f3a;padding:5px;border-radius:4px}.todo-container .todo-item .remove:hover *{background:#920000}